import React, { useEffect } from "react";
import log from "./agence-logo-black.png";
import Styles from "./Navbar.css";
import $ from "jquery";
import Work from "../Work/Work";
import Yellowdoor from "../Work/Yellowdoor/Yellowdoor";
import Scrolltotop from "../Scrolltotop";
import Landingpage from "../Landingpage/Landingpage";
import About from "../About/About";
import Contact from "../Contact/Contact";
import { Route } from "react-router";
import { NavLink, Link, Switch } from "react-router-dom";
import { CSSTransition, TransitionGroup } from "react-transition-group";

const Navbar = () => {
   useEffect(() => {
      const overlay = document.getElementById("overlay");

      const closeM = document.getElementById("close");

      const openM = document.getElementById("open");

      const Link = document.getElementById("lin");

      openM.addEventListener("click", function () {
         $(window).scrollTop(0);
         setTimeout(() => {
            overlay.classList.add("show");
         }, 50);
      });
      Link.addEventListener("click", function () {
         overlay.classList.remove("show");
      });
      window.addEventListener("scroll", function () {
         overlay.classList.remove("show");
      });
      closeM.addEventListener("click", function () {
         overlay.classList.remove("show");
      });
   });

   return (
      <div className="nav-bar">
         <NavLink to="/">
            <img
               className="log"
               src={require("./agence-logo-black.png")}
               alt=""
            />
         </NavLink>
         <nav id="overlay" className="over-lay">
            <svg
               className="cross"
               id="close"
               width="33"
               height="33"
               viewBox="0 0 18 17"
               fill="none"
               xmlns="http://www.w3.org/2000/svg"
            >
               <path
                  d="M1 2L17 15M1 15L17 2"
                  stroke="#094B3F"
                  strokeWidth="3"
               />
            </svg>

            <ul id="lin">
               <li>
                  <NavLink to="/Agence">Home</NavLink>
                  <span>Let's get start here</span>
               </li>
               <li>
                  <NavLink to="/about">About</NavLink>
                  <span>Find out what you need</span>
               </li>
               <li>
                  <NavLink to="/work">Work</NavLink>
                  <span>See what i've done</span>
               </li>
               <li>
                  <NavLink to="/contact">Contact</NavLink>
                  <span>Let me introduce myself</span>
               </li>
            </ul>
         </nav>

         <div className="container000">
            <svg
               className="menu"
               id="open"
               width="33"
               height="33"
               viewBox="0 0 19 16"
               fill="none"
               xmlns="http://www.w3.org/2000/svg"
            >
               <path
                  d="M0 2H18.5M0 8H18.5M0 14.5H18.5"
                  stroke="#000000"
                  strokeWidth="3"
               />
            </svg>
         </div>
         <Route
            render={({ location }) => (
               <Scrolltotop>
                  <TransitionGroup>
                     <CSSTransition
                        key={location.key}
                        timeout={300}
                        classNames="page"
                        unmountOnExit
                     >
                        <Switch location={location}>
                           <Route path="/" exact component={Landingpage} />
                           <Route
                              path="/Agence"
                              exact
                              component={Landingpage}
                           />

                           <Route path="/about" exact component={About} />
                           <Route path="/contact" exact component={Contact} />
                           <Route
                              path="/work/yellowdoor"
                              exact
                              component={Yellowdoor}
                           />
                        </Switch>
                     </CSSTransition>
                  </TransitionGroup>
                  <Route path="/work" exact component={Work} />
               </Scrolltotop>
            )}
         />
      </div>
   );
};

export default Navbar;
